import React from "react"
import { graphql } from "gatsby"
import { StateProvider } from "state/state"
import { reducer, initialState } from "state/reducer"
import Layout from "components/Layout"
import "../styles/contactus.scss"
import { Link } from "gatsby"
import { BLOCKS, MARKS, INLINES } from "@contentful/rich-text-types"
import { documentToReactComponents } from "@contentful/rich-text-react-renderer"
import { Container, Row, Col } from "react-bootstrap"
import SEO from "components/SEO"


const ContactUs = ({ data }) => {
  data = data.contentfulHcpContactUs;
  const options = {
    renderMark: {
      [MARKS.CODE]: text => (
        <span style={{ whiteSpace: "nowrap" }}>{text}</span>
      ),
    },
    renderNode: {
      [BLOCKS.PARAGRAPH]: (node, children) =>
      node.content[0].value !== "" ? children : null,
      [BLOCKS.HEADING_1]: (node, children) =>
      node.content[0].value !== "" ? <h1>{children}</h1> : null,
      [BLOCKS.HEADING_2]: (node, children) =>
        node.content[0].value !== "" ? <h2>{children}</h2> : null,
      [BLOCKS.UL_LIST]: (node, children) =>
        node.content[0].value !== "" ? <ul>{children}</ul> : null,
      [BLOCKS.LIST_ITEM]: (node, children) =>
      node.content[0].value !== "" ? <li>{children}</li> : null,
      [INLINES.HYPERLINK]: node => {
        if(node.data.uri === '/getstarted/'){
          return <Link to={node.data.uri}>{node.content[0].value}</Link>
        } else {
          return (
            <a href={node.data.uri} target={"_blank"} rel={"noopener noreferrer"}>
              {node.content[0].value}
            </a>
          )
        }
      },
    },
    renderText: text => {
      return text;
    },
  }
  return (
    <StateProvider initialState={initialState} reducer={reducer}>
  <Layout>
    <SEO title={data.title}></SEO>
    <section className="contactus">
      <Container>
        <Row>
          <Col lg={6}>
            {documentToReactComponents(data.headline.json,
                options
            )}
            <Col lg={8}>
              <img alt="" src={data.image.localFile.url} width="100%"></img>
            </Col>
          </Col>
          <Col lg={6}>
            {documentToReactComponents(data.content.json,
                options
              )}
          </Col>
        </Row>
      </Container>
    </section>
  </Layout>
    </StateProvider>
  )
}

export default ContactUs

export const query = graphql`
      query {
        contentfulHcpContactUs(title: {eq: "Contact Information"}) {
          title
          headline {
            json
          }
          image {
            localFile {
              url
            }
          }
          content {
            json
          }
        }
  }
`
